<script>
export default {
  data() {
    return {
      windowWidth: null,
      isMobile: null,
      isIOS: null
    }
  },
  created() {
    this.checkScreen()
    this.checkClient()
    window.addEventListener('resize', this.checkScreen)
  },
  methods: {
    checkScreen() {
      this.windowWidth = window.innerWidth
      this.windowWidth <= 750 ? this.isMobile = true : this.isMobile = false
    },
    checkClient() {
      this.isIOS = !!navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);// 判斷是否為iOS或MAC
    }
  }
  
}
</script>

