            <div class="cashback">
              <img src="../assets/images/icons/bigcoin.png" alt="">
              <div class="cashbackText">{{item.cashback}}</div>
            </div>
            <div class="price">
              <div class="offer">${{item.offer}}</div>
              <div class="originalPrice">${{item.price}}</div>
            </div>
            <div class="shop">
              <img src="../assets/images/icons/myfone.png" alt="">
              <div class="shopText">{{item.shop}}</div>
            </div>
            <div class="limitedTime">
              <img src="../assets/images/icons/ic_limited_time.png" alt="">
              <div class="time">{{timer[index].hh > 9 ? null:0}}{{timer[index].hh}}</div>&nbsp;:&nbsp;
              <div class="time">{{timer[index].mm > 9 ? null:0}}{{timer[index].mm}}</div>&nbsp;:&nbsp;
              <div class="time">{{timer[index].ss > 9 ? null:0}}{{timer[index].ss}}</div>
            </div>
          </div>
