<script>
  import {durationFormatter, getRemainTime} from '../assets/js/time'
  export default {
    data() {
      return {
        products: [ // 動態src取用靜態資源需用require進行連結 vue/webpack的一點坑 
          {product: 'HCG和成 香格里拉系列 雙體馬桶 CS140EJ', imageURL: require('../assets/images/items/cs140e.jpeg'), offer: 4990, price: 7990, endTime: '2021-12-29T12:00:00+08:00', cashback: '2%', shop: 'myfone購物'},
          {product: 'SAMSUNG三星｜Galaxy A71 8G/128G ', imageURL: require('../assets/images/items/a71.jpeg'), offer: 2990, price: 8990, endTime: '2021-12-23T01:52:00+08:00', cashback: '0~2%', shop: 'myfone購物'},
          {product: 'MSI 微星 | GeForce RTX™ 3070 VENTUS / GAMING X TRIO', imageURL: require('../assets/images/items/rtx3070.jpeg'), offer: 1990, price: 10990, endTime: '2021-12-29T13:00:00+08:00', cashback: '1%', shop: 'myfone購物'},
          {product: 'Mistral美寧｜貼心暖掛機 JR-5103HTT', imageURL: require('../assets/images/items/jr5103htt.jpeg'), offer: 1111, price: 9999, endTime: '2021-12-29T19:00:10+08:00', cashback: '1~2%', shop: 'myfone購物'},
          {product: 'SAMSUNG | Galaxy Buds Pro 真無線藍牙耳機', imageURL: require('../assets/images/items/galaxybudspro.jpeg'), offer: 1212, price: 66990, endTime: '2021-12-29T12:00:00+08:00', cashback: '2%', shop: 'myfone購物'},
          {product: 'SAMSUNG | Galaxy Buds Pro 真無線藍牙耳機', imageURL: require('../assets/images/items/galaxybudspro.jpeg'), offer: 39999, price: 66990, endTime: '2021-12-29T12:00:00+08:00', cashback: '2%', shop: 'myfone購物'},
          {product: 'SAMSUNG | Galaxy Buds Pro 真無線藍牙耳機', imageURL: require('../assets/images/items/galaxybudspro.jpeg'), offer: 60990, price: 66990, endTime: '2021-12-29T12:00:00+08:00', cashback: '2%', shop: 'myfone購物'},
          {product: 'SAMSUNG | Galaxy Buds Pro 真無線藍牙耳機', imageURL: require('../assets/images/items/galaxybudspro.jpeg'), offer: 29999, price: 66990, endTime: '2021-12-29T12:00:00+08:00', cashback: '2%', shop: 'myfone購物'},
          {product: 'SAMSUNG | Galaxy Buds Pro 真無線藍牙耳機', imageURL: require('../assets/images/items/galaxybudspro.jpeg'), offer: 1, price: 66990, endTime: '2021-12-29T12:00:00+08:00', cashback: '2%', shop: 'myfone購物'},
        ],
        duration: [],
        timer: [],
        num: 1,
        aaaa: 0
      }
    },
    created() {
      this.getTime()
    },  
    methods: {
      getTime() { // 將物件中的結束日期轉化為毫秒 並計算距離當下的時間
        for(let i=0; i<this.products.length; i++) {
          this.duration.push(getRemainTime(this.products[i].endTime))
          this.timer[i] = durationFormatter(this.duration[i])
          this.timer.splice(0,0)
        }
        setInterval(() => {
          for(let i=0; i<this.products.length; i++) {
            this.duration[i] > 0 ? this.duration[i] -= 1 : this.duration[i] = 0 // 當剩餘0秒時將秒數固定為0秒
            this.timer[i] = durationFormatter(this.duration[i])
          }
          // vue中透過索引修改數組值默認不會被監聽 所以直接重寫數組
          this.duration.splice(0,0)
          this.timer.splice(0,0)
        }, 1000)
      },
    },
  }
</script>

